.main-root {
    width: 100%;
    height: 100%;
    .btn {
        position: fixed;
        top: 42px;
        left: 1748px;
        display: flex;
        flex-flow: column;
        align-items: center;
        width: 151px;
        height: 300px;
        z-index: 100;
        .btn-cal {
           position: relative;
           width: 151px;
           img {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
           }
           .text {
                position: absolute;
                top: 26px;
                color: #fff;
                font-size: 32px;
                width: 100%;
                text-align: center;
           }
        }
    }
}
